 :root{
  --primary : #FFC107;
  --dark : #333;
  --light : #fff;
  --shadow : 0 1px 5px rgba(104,104,104,0.8);
 }






html, body {
  height: 100%;
  margin: 0;
  font-size: 16px;
  font-family: 'Times New Roman', Times, serif;
  font-weight: 400;
  line-height: 1.8em;
  color: #666;
} 


.pig1 , .pig2, .pig3{
  position: relative;
  opacity: 0.90;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  
}
.navbar{
  
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  height: 10%;
  padding: 50px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 24px;
  float: inherit;
  opacity: 1;
  margin: 1rem;
  
  background-color: #666;

}
.navbar .navbar-brand{
  font-display: inherit;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: #69F0AE;
  font-size: 30px;
  text-transform: uppercase;
  display: -ms-flexbox;
  display: flex;
}
.navbar a {

  
  
  opacity: 1;
  text-decoration: none;
  padding: 0.8rem;
  text-align: center;
  color: var(--light);
  text-transform: uppercase;
  font-size: 1.1rem;
  -webkit-box-shadow: var(--shadow);
          box-shadow: var(--shadow);
}
.navbar  li{
  display: grid;
  grid-gap: 1rem;
  align-self: right;
}
.btn button:hover{
  background: var(--dark);
  color: var(--light);
  display: block;
}

.navbar a:hover {

  background: var(--dark);
  color: var(--light);
}
.btn{

  background: var(--dark);
  color: var(--light);
  padding: 0.6rem, 1.3rem;
  text-decoration: none;
  text-transform: uppercase;
   border: 0;
   -webkit-box-shadow: var(--shadow);
           box-shadow: var(--shadow);
}
.pig1{
  background-image: url('./img/1_f_cover.png');
  min-height: 1000px;
  opacity: 1;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
 
  
  
}


